.current-weather-detail {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    text-transform: capitalize;
}

.weather-card {
    width: 11.5%;
    height: 10em;
    padding: 1em 1em;
    letter-spacing: 0.05em;
}

.weather-card .wi {
    font-size: 2em;
    color: #DB2B39;
}

.title-container {
    text-align: center;
}

.title-container h1 {
    margin-right: 0;
}

@media (max-width: 767px) {
    .weather-card {
        width: 12em;
    }
}


